<template>
  <section class="section section-catalog">
    <div class="container">
      <h3 class="section-title">Каталог продукции</h3>
      <div class="row flex-nowrap" @scroll="scrolled">
        <div class="col-12 categories" v-for="page of pageCount" :key="'page_' + page" :style="{'transform': `translateX(-${activePage * 100}%)`}" :class="{'active': page-1 === activePage}">
          <div class="row justify-content-start categories-page">
            <div class="" v-for="(category, index) of getPageCategories(page-1)" :key="index">
              <category-block :category="category"></category-block>
            </div>
          </div>
        </div>
      </div>
      <div class="text-right mt-2 pr-4">
        <router-link to="catalog" class="link">
          вся продукция>>
        </router-link>
      </div>
      <div class="row mb-3">
        <div class="dots mx-auto">
          <div class="dot" v-for="i of pageCount" :class="{'active': i===activePage+1}" :key="i" @click="activePage = i-1"></div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import CategoryBlock from '../../../components/CategoryBlock'
  import {createNamespacedHelpers} from 'vuex'
  import {NONE} from '../../../store/category-types'

  const {mapGetters} = createNamespacedHelpers('special')

  export default {
    name: 'section-catalog',
    components: {CategoryBlock},
    data(){
      return {
        activePage: 0,
        limit: 8,
        sortBy: NONE
      }
    },
    computed: {
      ...mapGetters({
        categories: 'getCategories'
      }),
      pageCount() {
        return Math.ceil(this.categories.length / this.limit)
      },
      pageCategories() {
        return this.categories.slice(this.limit*this.activePage, this.limit*(this.activePage+1))
      }
    },
    methods: {
      getPageCategories(page) {
        return this.categories.slice(this.limit*page, this.limit*(page+1))
      },
      scrolled() {
        console.log('scrolled')
      }
    },
    mounted() {

    }
  }
</script>

<style scoped lang="scss">
  h3 {
    margin-bottom: 7.5rem;
  }
  .container {
    max-width: 128rem;
  }
  .categories {
    opacity: 0;
    transition: transform .2s ease-in, opacity .2s ease-in;
    pointer-events: none;
    &.active {
      pointer-events: all;
      opacity: 1;
      transition: transform .2s .1s ease-out, opacity .2s .1s ease-out;
    }
  }
  .categories-page {
    width: auto;
    margin-left: auto;
    margin-right: auto;
    & > div {
      padding-left: 1.5rem;
      padding-right: 1.5rem;
      margin-bottom: 2.1rem;
    }
  }
  .link {
    color: #ff7821;
    font-size: 1.8rem;
    font-weight: bold;
  }
</style>